








































import {Component, Emit, Inject, Model, Prop, Provide, Vue, Watch} from 'vue-property-decorator';


class ConsultationDataModel {
    public consultationId : string = "";
    public consultationType : string = "";
    public consultationName : string = "";
    public question : string = "";
    public answer : string = "";
    public isShow : boolean = false;
}




@Component({
    components: {}
})
export default class ConsultationItem extends Vue {

    @Prop({
        default() {
            return new ConsultationDataModel();
        }
    })
    consultationData : ConsultationDataModel | undefined;




    private handleChange(data : boolean) {
        console.log("变化了", data);
    }

    @Watch("consultationData.isShow")
    handle(newVal : boolean) {
        this.handleChange(newVal);
    }

    handleDelete() {
        this.$emit("delete", this.consultationData?.consultationId);
    }
    handleReply() {
        this.$emit("reply", this.consultationData?.consultationId);
    }


}
